.service-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.form-element {
    display: block;
}

.form-actions {
    display: flex;
    justify-content: flex-end;
    margin: 2rem 1rem;
}

.form-title {
    margin-bottom: .7rem;
}

.form-result {
    margin: auto 1rem;
}